html, body {
  margin: 0;
  font-family: Roboto, -apple-system, BlinkMacSystemFont, 'Segoe UI', Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  color: white;
  height: 100%;
  width: 100%;
}

/* Tents and tree */
.tents_and_tree_row {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  flex: 30px 0 0;
}

.tents_and_tree_cell {
  border: 2px solid black;
  position: relative;
  text-align: center;
  width: 100px;
  height: 100px;
  padding-bottom: 100px;
}

.tents_and_tree_cell_value-0 {
  background-color: rgb(50, 50, 50);
}

.tents_and_tree_cell_value-1 {
  background: #01a000;
}

.tents_and_tree_cell_value-2 {
  background: rgb(127, 0, 0);
}

.tents_and_tree_cell_value-3 {
  background: rgb(114, 77, 21);
}

.tents_and_tree_tent_number_indicator{
  position: relative;
  text-align: center;
  width: 100px;
  height: 100px;
  padding-bottom: 100px;
}

.tents_and_tree_tent_number_indicator-number {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
  font-size: 65px;
}

/* Game of life */
.game_of_life_table {
  border-collapse: collapse;
}

.game_of_life_row {
  border: 1px solid black;
}

.game_of_life_cell {
  width: 15px;
  height: 15px;
  border: 1px solid black;
}

.game_of_life_cell-0 {
  background-color: rgb(50, 50, 50);
}

.game_of_life_cell-1 {
  background-color: white;
}